const Feedback = async ({ params }: RouteParams) => {
  const { id } = await params;
  const user = await getCurrentUser();

  const interview = await getInterviewById(id);
  if (!interview) redirect("/");

  const feedback = await getFeedbackByInterviewId({
    interviewId: id,
    userId: user?.id!,
  });

  return (
    <div className="relative w-screen h-screen min-h-screen overflow-x-hidden bg-neutral-900 text-white">
      {/* Fullscreen Star Background */}
      <div className="fixed inset-0 z-0">
        <StarsBackground />
        <ShootingStars />
      </div>

      {/* Content on top */}
      <main className="relative z-10 w-full max-w-5xl mx-auto px-6 py-20 overflow-y-auto">
        <h1 className="text-4xl md:text-5xl font-bold text-center mb-12">
          Feedback on the <span className="capitalize">{interview.role}</span> Interview
        </h1>

        <div className="flex flex-wrap justify-center gap-6 mb-8 text-base">
          <div className="flex items-center gap-2">
            <Image src="/star.svg" width={22} height={22} alt="star" />
            <span>
              Overall Impression:{" "}
              <span className="text-green-400 font-bold">{feedback?.totalScore}</span>/100
            </span>
          </div>
          <div className="flex items-center gap-2">
            <Image src="/calendar.svg" width={22} height={22} alt="calendar" />
            <span>
              {feedback?.createdAt
                ? dayjs(feedback.createdAt).format("MMM D, YYYY h:mm A")
                : "N/A"}
            </span>
          </div>
        </div>

        <div className="w-full h-2 bg-gray-700 rounded-full mb-10">
          <div
            className="h-full bg-green-500 rounded-full transition-all duration-500"
            style={{ width: `${(feedback?.totalScore || 0)}%` }}
          />
        </div>

        <p className="text-lg mb-10 text-neutral-200">{feedback?.finalAssessment}</p>

        <div className="mb-10">
          <h2 className="text-2xl font-semibold mb-4 text-primary-200">Breakdown of the Interview:</h2>
          <div className="space-y-4">
            {feedback?.categoryScores?.map((category, index) => (
              <div
                key={index}
                className="bg-neutral-800 border border-neutral-700 p-4 rounded-lg shadow"
              >
                <p className="font-bold text-lg">
                  {index + 1}. {category.name} ({category.score}/100)
                </p>
                <p className="text-neutral-400">{category.comment}</p>
              </div>
            ))}
          </div>
        </div>

        <div className="mb-10">
          <h3 className="text-xl font-semibold text-green-400 mb-2">Strengths</h3>
          <ul className="list-disc list-inside text-neutral-300">
            {feedback?.strengths?.map((item, i) => (
              <li key={i}>{item}</li>
            ))}
          </ul>
        </div>

        <div className="mb-10">
          <h3 className="text-xl font-semibold text-red-400 mb-2">Areas for Improvement</h3>
          <ul className="list-disc list-inside text-neutral-300">
            {feedback?.areasForImprovement?.map((item, i) => (
              <li key={i}>{item}</li>
            ))}
          </ul>
        </div>

        <div className="flex flex-col md:flex-row gap-4 mt-10">
          <Button className="btn-secondary w-full md:w-1/2">
            <Link href="/" className="w-full text-center text-sm font-semibold text-primary-200">
              Back to Dashboard
            </Link>
          </Button>

          <Button className="btn-primary w-full md:w-1/2">
            <Link href={`/interview/${id}`} className="w-full text-center text-sm font-semibold text-black">
              Retake Interview
            </Link>
          </Button>
        </div>
      </main>
    </div>
  );
};
